import React from 'react';
import { Button} from 'reactstrap';

const Customercomponent = () => {
    
    return (
        <div className="container">
            <div className="row">
                <div className="col-md-12 col-sm-12">
                    <div className="mt-5 d-flex justify-content-between flex-wrap">
                        <img src={'/image/review1.png'} alt="review1.png" />
                        <img src={'/image/review2.png'} alt="review2.png" />
                   </div>
                </div>
            </div>
            <div className="row">
                <div className="col-md-5 col-sm-12 m-auto">
                 <div className="text-center">
                        <h4>Need a super hero?</h4>
                        <p className="text-info mt-4"><small>Do you require some help for your project: Conception workshop,
                            prototyping, marketing strategy, landing page, Ux/UI?</small></p>
                           <div className="row">
                            <div className="col-md-5  m-auto">
                                <Button className="border-0 mt-3 p-2 w-100"
                                style={{ backgroundColor: '#FF9900' }}>Contact our expert</Button>
                            </div>
                          </div>
                      </div>
               </div>
            </div>
            <div className="row">
                <div className="col-md-12 col-sm-12">
                    <div className="mt-5 d-flex justify-content-between flex-wrap">
                        <img src={'/image/review3.png'} alt="review3.png" />
                        <img src={'/image/review5.png'} alt="review5.png" />
                   </div>
                </div>
            </div>
            <div className="row">
              <div className="col-md-5 col-sm-12 m-auto">
                        <img src={'/image/review4.png'} alt="review4.png" />
                </div>
            </div>
        </div>
    );

}




export default Customercomponent;