import React, { useState } from 'react';
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  Button
} from 'reactstrap';

import {NavLink} from 'react-router-dom';

const Navigation = (props) => {
  const [isOpen, setIsOpen] = useState(false);

  const toggle = () => setIsOpen(!isOpen);

  return (
   
      <Navbar color="light" light expand="md">
         <div className="container">
              <NavbarBrand href="/">
                  <img src={'/image/Logo.png'} alt="Logo.png"/>
            </NavbarBrand>
            <NavbarToggler onClick={toggle} />
            <Collapse isOpen={isOpen} navbar>
                   <Nav className="m-auto" navbar>
                    <NavItem>
                    <NavLink to="/Product"  className="nav-link text-dark">Product</NavLink>
                    </NavItem>
                    <NavItem>
                        <NavLink to="/Customer" className="nav-link text-dark">Customers</NavLink>
                    </NavItem>
                    <NavItem>
                        <NavLink to="/Pricing" className="nav-link text-dark">Pricing</NavLink>
                    </NavItem>
                    <NavItem>
                        <NavLink to="/Resources" className="nav-link text-dark">Resources</NavLink>
                    </NavItem>
                   </Nav>
                <Button className="m-2 text-dark"style={{backgroundColor:"#FFFFFF",border:'1px solid #BCD0E5'}}>Sing in</Button>
                <Button className="m-2 text-light border-0" style={{backgroundColor:'#FF9900'}}>Sing Up</Button>
            </Collapse>    
        </div>
      </Navbar>
 
  );
}

export default Navigation;

                     