import React from 'react';
import {Button} from 'reactstrap';

const Servicecomponent = () => {
    
    return (
        <div className="container wrapperPloy">
            <div className="row mt-5">
                <div className="col-md-6 col-sm-12 m-auto">
                    <h3 className="text-center">We design tools to unveil your </h3>
                    <h4 className="text-center">superpowers</h4>
              </div>
            </div>
            <div className="row mt-5">
                <div className="col-md-3 col-sm-12 m-auto">
                    <div>
                        <img src={'/image/icon1.png'} alt="icon1.png" className="mx-auto d-block"/>
                        <h6 className="text-center">First click tests</h6>
                        <p className="text-center">While most people enjoy casino gambling,</p>
                    </div>
                </div>
                <div className="col-md-3 col-sm-12 m-auto">
                    <div>
                        <img src={'/image/icon2.png'} alt="icon1.png" className="mx-auto d-block"/>
                        <h6 className="text-center">Design surveys</h6>
                        <p className="text-center">Sports betting, lottery and bingo playing for the fun</p>
                    </div>
              </div>
              <div className="col-md-3 col-sm-12 m-auto">
                    <div>
                        <img src={'/image/icon3.png'} alt="icon1.png" className="mx-auto d-block"/>
                        <h6 className="text-center">Preference tests</h6>
                        <p className="text-center">The Myspace page defines the individual.</p>
                    </div>
              </div>
              <div className="col-md-3 col-sm-12 m-auto">
                    <div className="border-1">
                        <img src={'/image/icon4.png'} alt="icon1.png" className="mx-auto d-block"/>
                        <h6 className="text-center">Five second tests</h6>
                        <p className="text-center">Personal choices and the overall personality of the person. </p>
                    </div>
              </div>
            </div>
            <div className="row mt-5">
                <div className="col-md-2 col-sm-1 m-auto">
                    <Button className="text-uppercase border-0 p-2 w-100 mt-3"
                        style={{ backgroundColor: "#FF9900" }}>SIGN UP NOW</Button>
               </div>
            </div>
        </div>
    );

}



export default Servicecomponent;