import React from 'react';
import {Form,Input,InputGroup,Button} from 'reactstrap';
import  '../Customstyle/style.css';


const Footer = () => {
    
    return (
        <div className="container">
            <div className="footerWrap">
            <div className="row mt-5 ">
                <div className="col-md-3 col-sm-12 mb-3">
                    <img src={'/image/Logo.png'} alt="Logo.png"/>
                </div>
                <div className="col-md-3 col-sm-12">
                    <div>
                        <h5 className="mb-4 ">Quick Links</h5>
                        <ul className="footerList">
                            <li><a href="/">About Us</a></li>
                            <li><a href="/">Blog</a></li>
                            <li><a href="/">Contact</a></li>
                            <li><a href="/">FAQ</a></li>
                        </ul>
                   </div>
                </div>
                <div className="col-md-3 col-sm-12 ">
                <div>
                      <h5 className="mb-4 ">Legal Stuff</h5>
                        <ul className="footerList">
                            <li><a href="/">Disclaimer</a></li>
                            <li><a href="/">Disclaimer</a></li>
                            <li><a href="/">Financing</a></li>
                            <li><a href="/">Privacy Policy</a></li>
                            <li><a href="/">Terms of Service</a></li>
                        </ul>
                   </div>
                </div>
                <div className="col-md-3 col-sm-6 ">
                    <div>
                        <h5 className="mb-3 ">knowing you're always on the best energy deal. </h5>
                    </div>
                    <div>
                        <Form>
                            <InputGroup className="mb-3">
                                   <Input type="text" placeholder="Enter your phone Number" className="w-100"/>
                                </InputGroup>

                                <InputGroup>
                                    <Button
                                        className="border-0"
                                        style={{ backgroundColor: "#FF9900" }}>
                                        Sign up Now</Button>
                             </InputGroup>
                        </Form>
                    </div>
                </div>
           </div>
           </div>
        </div>
    );

}




export default Footer;