import React from 'react';
import { Button,CardText} from 'reactstrap';
const Slidercomponent = () => {
    
    return (
            <div className="container">
            <div className="row mt-3">
                <div className="col-md-6 mt-5">
                    <h3>The Design Thinking</h3>
                    <h4>superpowers</h4>
                    <p className="mt-4">Tools, tutorials, design and innovation experts, all in one place! The most intuitive way to imagine your next user experience.</p>
                    <div className="d-flex flex-wrap">
                        <Button className="m-2" size="md" style={{backgroundColor:"#FF9900",border:'0px'}}>Get started</Button>
                        <CardText className="m-2">
                            <img src={'/image/videoplay.png'} alt="videoplay.png" className="m-1"/>
                             <a href="/" style={{color:"#FF9900"}}>  Watch the Video</a>
                         </CardText>
                    </div>
                </div>
                <div className="col-md-6">
                    <img src={'/image/slider.png'} alt="slider.png" className="w-100"/>
               </div>
            </div>
        </div>
     
    );

}


export default Slidercomponent;